.conteo{
    opacity: 0;
    animation: fadeIn 1s forwards 0.9s;
    position: relative;
    text-align: center;
    width: 100%;
    height: 100vh;
}

@keyframes fadeIn{
    0% {opacity: 0}
    100% {opacity: 1}
}

.logofep_conteo{
    background: url('../../assets/img/generales/logofep.png');
    background-size: cover;
    background-repeat: no-repeat;
    width: 330px;
    height: 105px;
    margin: 0px auto;
    margin-top: 15px;
    margin-bottom: 20px;
}

.logofep_festival{
    width: 38%;
    margin: 0px auto;
    margin-top: 15px;
    cursor: pointer;
    transition: 1s;
}

.logofep_festival:hover{
    transform: scale(1.1);
}



#demo{
    font-size: 100px;
    font-family: 'TitlingGothicFBWide-Bold';
    padding: 0 30px;
}

#demo > div{
    display: flex;
    justify-content: center;

}

#demo div div{
    display: flex;
    flex-direction: column;
    margin: 0 35px;
}

#demo p{
    font-size: 12px;
    margin-top: -10px;
    color: #fda302;
    text-transform: uppercase;
}

#demo span{
    display: inline-block;
    color: #fda302;
    margin: 0 15px;
}

.tiempos {
    display: flex;
    justify-content: center;
}

.contenedor{
    transition: 0.5s;
}

.contenedor a{
    color: #2c267b !important;
    text-decoration: none;
}

.contenedor img{
    cursor: pointer;
    width: 20%;
    display: block;
    margin: 0px auto;
}

.beneficios{
    width: 10% !important;
}
.mensaje_roll{
    width: 17% !important;
    transform: translate(5px, 28px);
    opacity: 0;
    transition: 0.5s all;
}

.contenedor_form{
    position: absolute;
    width: 100%;
    transform: translate(0px, -50%);
    opacity: 0;
    visibility: hidden;
    transition: 1s;
}


.contenedor_form2{
    width: 100%;
    transform: translate(0px, -25%);
}

.content_colombia{
    font-family: 'TitlingGothicFBWide-Reg';
    font-size: 15px;
    padding: 25px;
}


.fadeOut{
    opacity: 0;
    visibility: hidden;
}

.fadeIn_form{
    opacity: 1 !important;
    visibility: visible !important;
}

/* MODAL */
.modal_contenedor_vip{
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    position: absolute;
    width: 100%;
    height: 100vh;
    z-index: 20;
    opacity: 0;
    visibility: hidden;
    transition: 1s;
}

.modal_contenedor_vip .modal_vip{
    width: 50%;
}

.mockup{
    width: 100%;
}

.entendido{ 
    width: 30%;
    transform: translate(100%, -130%);
    cursor: pointer;
}

.show_modal{
    opacity: 1;
    visibility: visible;
}

@media only screen and (min-width: 2000px) and (min-height: 1000px){


    .content_colombia {
        font-size: 25px;
    }

}

@media only screen and (min-width: 1000px) and (min-height: 500px) and (max-height: 550px){
    .contenedor_form {
        transform: translate(0px, -55%);
    }

    .logofep_conteo {
        margin-bottom: -10px;
        width: 220px;
        height: 70px;
    }

    .logofep_festival {
        width: 28% !important;
        margin-bottom: 0;
    }

    .mensaje_roll {
        width: 13% !important;
    }

    select {
        padding: 3px 20px;
    }

    input[type="submit"] {
        padding: 7px 13px;
        font-size: 12px;
    }

    .mensaje {
        width: 30% !important;
    }

    .contenedor img {
        width: 15%;
    }

}

@media only screen and (min-width: 1000px) and (min-height: 550px) and (max-height: 650px) {
    .contenedor_form {
        transform: translate(0px, -55%);
    }

    .logofep_conteo {
        margin-bottom: -10px;
        width: 220px;
        height: 70px;
    }

    .logofep_festival {
        width: 30%;
        margin-bottom: 0;
    }

    .mensaje_roll {
        width: 15% !important;
    }


}

@media only screen and (min-width: 1000px)  and (min-height: 650px) and (max-height: 700px) {
    .form div label {
        font-size: 11px !important;
    }
   
    .contenedor_form {
        transform: translate(0px, -55%);
    }

    .logofep_festival {
        width: 36%;
        margin-bottom: 0;
    }


}

@media only screen and (max-width: 768px){
    .contenedor_form {
        transform: translateY(-50%);
    }
}

@media only screen and (max-width: 768px){
    .logofep_conteo {
        width: 250px;
        height: 79px;
    }
    .contenedor{
        display: block;
    }

    .contenedor div {
        margin: 0px auto;
        width: 50%;
        margin-bottom: 30px;
        margin-top: 25px;
        font-size: 10px;
        padding: 15px 0px;
    }

    .contenedor_form {
        transform: translate(0px, -35%);
    }

    .logofep_festival {
        width: 50%;
    }

    .mensaje_roll {
        width: 55%;
        display: none;
    }



}

@media only screen and (max-width: 430px){

    .contenedor img {
        width: 55%;
    }

    .beneficios {
        width: 35% !important;
    }


    .logofep_festival {
        width: 80%;
    }

    .contenedor_form {
        transform: translate(0px, -55%) ;
    }


    .logofep_conteo {
        margin-top: 65px;
        margin-bottom: 30px;
    }

    .content_colombia {
        width: 90%;
        margin: 0px auto;
        max-width: 250px;
    }

    .modal_contenedor_vip {
        justify-content: left;
        top: 0;
        position: fixed;

    }

    .modal_contenedor_vip .modal_vip {
        width: 86%;
    }
    .mensaje_roll {
        width: 50% !important;
        animation: fadeInRoll 7s forwards;
        opacity: 1;
    }

    @keyframes fadeInRoll {
        0% {opacity: 1;}
        90% {opacity: 1;}
        100% {opacity: 0;}
    }
    
}

@media only screen and (max-width: 320px){
    #demo {
        font-size: 20px;
    }

    
    .contenedor_form {
        transform: translate(0px, -50%);
    }
    
}