.bienvenida{
    transition: 1s;

}

.bienvenida_contenedor{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 1300px;
    height: 100vh;
    width: 100%;
    margin: 0px auto;
    position: relative;
    z-index: 1;
}

.bienvenida_contenedor h2{
    font-size: 20px; 
    margin: 50px 0px 0px 0px;
    font-family: 'TitlingGothicFBWide-Bold';
}

.logofep{
    background: url('../../assets/img/generales/logofep.png');
    background-size: cover;
    background-repeat: no-repeat;
    width: 645px;
    height: 200px;
}



.fadeIn{
    opacity: 0;
}

.mayor_edad{
    display: flex;
    justify-content: center;
}



.mayor_edad div{
    font-family: 'TitlingGothicFBWide-Bold';
    cursor: pointer;
    color: #2c267b;
    background: white;
    padding: 20px 0px;
    margin: 20px 20px;
    font-size: 15px;
    text-align: center;
    width: 65px;
    transition: 0.2s;
}

.mayor_edad div:hover{
    background:#fda302;
}

/* MODAL */
.modal_contenedor{
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    position: absolute;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.37);
    -webkit-backdrop-filter: blur(6px);
    backdrop-filter: blur(6px);
    z-index: 20;
    opacity: 0;
    visibility: hidden;
    transition: 1s;
}

.modal{
    padding: 60px;
    width: 500px;
    height: 250px;
    background: #fda302;
    display: flex;
    align-items: center;
}

.modal p{
    font-family: 'TitlingGothicFBWide-Bold';
    color: #2c267b;
    font-size: 25px;
}

.show_modal{
    opacity: 1;
    visibility: visible;
}

.terminos{
    width: 30%;
    text-align: center;
    font-size: 10px;
    margin-top: 20px;
}

.terminos .url_terminos{
    color: #fda302;
    cursor: pointer;
    text-decoration: none;
}

@media only screen and (min-width: 1000px) and (min-height: 500px) and (max-height: 580px){
    .logofep {
        background-repeat: no-repeat;
        width: 335px;
        height: 100px;
    }
}

@media only screen and (max-width: 430px){
    .terminos {
        width: 60%;
    }

    .logofep {
        width: 250px;
        height: 80px;
    }

    .bienvenida_contenedor h2 {
        font-size: 13px;
    }

    .mayor_edad{
        display: block;
    }

    .mayor_edad div{
        margin: 0px auto;
        margin-top: 25px;
    }

    
    
}