
.App{
  background: url('./assets/img/generales/fondo.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  
  width: 100%;
  height: 100vh;
  position: relative;

  margin: 0;
  padding: 0;
  overflow-y: hidden;
  overflow-x: hidden;
  color: white;
}

.content_off{
  transition: 1s;
}

.terminos_contenedor{
  background: url('../src/assets/img/generales/fondo.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  width: 50%;
  margin: 0px auto;
  text-align: center;
  margin-top: 65px;
  height: calc(100vh - 200px);
  overflow: scroll;
  position: absolute;
  right: 0;
  left: 0;
  overflow-x: hidden;
  padding: 20px;
}

.terminos_contenedor p{
  text-align: justify;
}

.terminos_contenedor p span{
  display: block;
  text-align: right;
  padding: 10px 0;
  cursor: pointer;
}

.terminos_contenedor p span .back{
  color: #fda302;
  text-decoration: none;
}

