.fadeCo{
  opacity: 0;
  visibility: hidden;
  animation: fadeInCo 2s forwards 2s;

}

@keyframes fadeInCo{
  from {opacity: 0;
    visibility: hidden;}
  to {opacity: 1;
    visibility: visible;}

}

