  background: url('./assets/img/generales/lineup.png');
  background-size: contain;
  background-repeat: no-repeat;
  position: absolute;
  width: 4%;
  height: 8%;
  right: 12%;
  top: 1%;
  z-index: 5000;
  cursor: pointer;
}

.lineUp{
  position: fixed;
  z-index: 100;
  left: 0;
  right: 0;
  margin: 0px auto;
  transform: translate(0px, 4%);
  text-align: center;
  transition: 1s;
  visibility: hidden;
  opacity: 0;
  width: 35%;
}

.lineUp img{
  width: 35%;
}

.lineUpMovil{
  position: fixed;
  z-index: 100;
  left: 0;
  right: 0;
  margin: 0px auto;
  transform: translate(0px, 10%);
  text-align: center;
  transition: 1s;
  visibility: hidden;
  opacity: 0;
}

.lineUpMovil img{
  width: 72%;
}

.cerrar{
  font-size: 25px;
  position: absolute;
  z-index: 2000;
  right: 5%;
  cursor: pointer;
  color: black;
  text-align: right;
  width: 25%;
  padding: 15px;
  font-weight: 600;
}

.fadeInLine{
  opacity: 1;
  visibility: visible;
}

.tienda_fep{
  background: url('./assets/img/generales/tiendafep.png');
  background-size: contain;
  background-repeat: no-repeat;
  position: absolute;
  width: 12%;
  height: 6%;
  right: 16%;
  top: 0%;
  z-index: 5000;
  cursor: pointer;
}

/* ICONOS DE FOOTER */
.logos_footer {
  position: absolute;
  width: 100%;
}

.logos_footer img{
  width: 100%;
  position: fixed;
  bottom: 0;
 }

.logos_footer .lateral{
  position: fixed;
  bottom: 1%;
  width: 0.7%;
  left: 1%;
}

.logos_footer .centro{
  position: fixed;
  bottom: 0.5%;
  width: 40%;
  right: 0;
  left: 0;
  margin: 0px auto;
}




 .movil{
