/* FORMULARIO */
.formulario{
    margin: 35px 0;
    position: relative;
}

.form{
    width: 100%;
    max-width: 500px;
    margin: 0px auto;
}

.form div label{
    font-size: 15px;
    width: 85%;
    display: inline-block;
    padding-top: 25px;  
    padding-bottom: 15px;
    color: white;
    font-family: 'TitlingGothicFBWide-Bold';
}

input[type="text"] {
    background: white;
    border: none;
    border-radius: 14px;
    color: #2c267b;
    width: 53%;
    text-align: center;
    padding: 5px;
    font-size: 15px;
}

select{
    background: white;
    border: none;
    border-radius: 14px;
    color: #2c267b;
    width: 53%;
    text-align: center;
    padding: 5px 20px;
    cursor: pointer;
}

option{
    color: #2c267b;
    cursor: pointer;

}

input[type="submit"]{
    background: #fda302;
    border: none;
    padding: 10px 15px;
    margin-top: 15px;
    margin-bottom: 5px;
    cursor: pointer;
    color:  #2c267b;
    font-family: 'TitlingGothicFBWide-Bold';
}

.mensaje{  
   width: 35%;
   opacity: 0;
   transition: 1s;
}

.mensaje_show{
    opacity: 1;
}

.iconowa{
   width: 7%;
   cursor: pointer;
}
.heartbeat {
	-webkit-animation: heartbeat 4s ease-in-out ;
	        animation: heartbeat 4s ease-in-out ;
}

 @-webkit-keyframes heartbeat {
    from {opacity: 1;
      -webkit-transform: scale(1);
              transform: scale(1);
      -webkit-transform-origin: center center;
              transform-origin: center center;
      -webkit-animation-timing-function: ease-out;
              animation-timing-function: ease-out;
    }
    10% {opacity: 1;
      -webkit-transform: scale(0.91);
              transform: scale(0.91);
      -webkit-animation-timing-function: ease-in;
              animation-timing-function: ease-in;
    }
    17% {opacity: 1;
      -webkit-transform: scale(0.98);
              transform: scale(0.98);
      -webkit-animation-timing-function: ease-out;
              animation-timing-function: ease-out;
    }
    33% {opacity: 1;
      -webkit-transform: scale(0.87);
              transform: scale(0.87);
      -webkit-animation-timing-function: ease-in;
              animation-timing-function: ease-in;
    }
    45% {opacity: 1;
      -webkit-transform: scale(1);
              transform: scale(1);
      -webkit-animation-timing-function: ease-out;
              animation-timing-function: ease-out;
    }
    90%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
  }
  @keyframes heartbeat {
    from {opacity: 1;
