.horario{
    width: 25%;
}


.acceder_tienda{
    margin: 0px 0px;
    padding: 10px 25px;
    background: #2c267b;
    color: #fda302;
    cursor: pointer;
    text-align: center;
    box-shadow: 10px 10px 23px -9px rgba(0,0,0,0.75);
    display: block;
    margin: 20px 0px;
}

.acceder_tienda a{
    color: #fda302;
    text-decoration: none;
    text-transform: uppercase;
}

.success_form{
    color: #2c267b;
    width: 45%;
    max-width: 600px;
    background: #fda302;
    margin: 0px auto;
    box-shadow: 10px 10px 23px -9px rgba(0,0,0,0.75);
    padding: 10px 40px;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    transition: 1s;
    z-index: 2000;
    visibility: hidden;
    opacity: 0;
    padding-bottom: 60px;
}

.failed_form{
    color:  #2c267b;
    width: 30%;
    max-width: 500px;
    background: #fda302;
    margin: 0px auto;
    -webkit-box-shadow: 10px 10px 23px -9px rgba(0,0,0,0.75);
    -moz-box-shadow: 10px 10px 23px -9px rgba(0,0,0,0.75);
    box-shadow: 10px 10px 23px -9px rgba(0,0,0,0.75);
    padding: 25px 40px;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    transition: 1s;
    visibility: hidden;
    opacity: 0;
}

.failed_form  img{
    display: block;
    margin: 0px auto;
    width: 15%;
    margin-top: 10px;
    margin-bottom: 10px;
}

.success_form  img{
    display: block;
    margin: 0px auto;
    width: 10%;
    margin-top: 15px;
    margin-bottom: 15px;
}

.failed_form  p{
    border-radius: 14px;
    background: white;
    color: #2c267b;
    margin-bottom: 20px;
}

.success_form  p{
    border-radius: 14px;
    color: #2c267b;
    margin: 0px auto;
    margin-bottom: 20px;
    font-size: 15px;
    padding: 4px;
    display: block;
    width: 85%;
    font-family: 'TitlingGothicFBWide-Bold';
    padding: 4px;
}

@media only screen and (min-width: 1000px) and (min-height: 500px) and (max-height: 550px){
    .form div label {
        font-size: 11px;
        padding-top: 15px;
        padding-bottom: 2px;
    }
}

@media only screen and (min-width: 1000px) and (min-height: 550px) and (max-height: 650px){
    .form div label {
        font-size: 11px;
        padding-top: 15px;
        padding-bottom: 2px;
    }
}

@media only screen and (max-width: 768px){
    .failed_form {
        width: 50%;
    }

    .success_form{
        width: 50%;
    }

    .form div label {
        font-size: 10px;
        padding-top: 15px;
        padding-bottom: 5px;
    }

}

@media only screen and (max-width: 430px){
    .failed_form {
        width: 75%;
    }

    .success_form{
        width: 75%;
    }

    .success_form p {
        font-size: 13px;
        padding: 4px;
    }

}